let offset = 0
let total = 10
export default {
	data() {
		return {
			list: [],
			isLoading: false
		};
	},
	onLoad() {
		this.init()
	},
	onUnload() {
		offset = 0;
	},
	methods: {
		init() {
			this.api.helper.get_query_list({
				type: 1,
				ctime: parseInt(Date.now()),
				offset,
				total
			}, res => {
				console.log(res)
				if (res.data.length > 0) {
					this.list = res.data
				}
				setTimeout(()=>{
					this.isLoading = false
				},500)
			})
		},
		exchange() {
			offset = offset + total
			this.isLoading = true
			this.init()
		},
		goList(id){
			uni.navigateTo({
				url:"../list/list?id="+id
			})
		}
	}
}
